import React, { Fragment } from 'react';
import CurrencySelector from '../../../../shared/CurrencySelector.js';

import FieldHeader from '../../../../shared/FieldHeader.js';

const FreightCurrencyCode = () => {
  const terms = ['EXW', 'FCA', 'FAS', 'FOB'];
  return (
    terms.includes(localStorage.getItem('tradeTerms')) && (
      <Fragment>
        <FieldHeader>ค่าประกัน</FieldHeader>
        <CurrencySelector
          required
          name="freightCurrencyCode"
          label="Freight Currency Code"
          placeholder="ค้นหาด้วยสกุลเงิน / ชื่อประเทศ"
        />
      </Fragment>
    )
  );
};

export default FreightCurrencyCode;
