import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment, withWidth } from '@material-ui/core';
import AdditionalInfoDialog from './AdditionalInfoDialog';
import PropTypes from 'prop-types';
import { addCustomValidationRules } from './customValidations';

class RegularTextField extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      [props.name]: '',
    };
  }

  handleBlur = e => {
    const value = e.target.value;
    if (this.props.isNumber && !isNaN(parseInt(value))) {
      this.setState({
        [this.props.name]: parseInt(value).toString(),
      });
    } else {
      this.setState({
        [this.props.name]: value.trim(),
      });
    }
  };

  handleChange = e => {
    this.setState({
      [this.props.name]: e.target.value,
    });
  };

  componentDidMount() {
    addCustomValidationRules(this);
    this.updateInterval = setTimeout(() => {
      this.forceUpdate();
    }, 1000);
  }

  componentWillUnmount() {
    clearTimeout(this.updateInterval);
  }

  render() {
    const {
      name,
      label,
      helperText,
      placeholder,
      validators,
      errorMessages,
      required,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
      width,
      multiline,
      rows,
      disabled,
    } = this.props;
    return (
      <TextValidator
        disabled={disabled}
        name={name}
        id={name}
        required={required}
        onBlur={this.handleBlur}
        onChange={this.handleChange}
        value={this.state[`${name}`]}
        label={label}
        multiline={multiline}
        rows={rows}
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        helperText={helperText}
        margin={width === 'xs' ? 'normal' : 'dense'}
        placeholder={placeholder}
        variant="outlined"
        fullWidth
        validators={validators}
        errorMessages={errorMessages}
        InputProps={{
          endAdornment: dialogContent && (
            <InputAdornment position="end">
              <AdditionalInfoDialog
                maxWidth={dialogWidth ? dialogWidth : 'xs'}
                titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                titleEN={label}
                format={dialogFormat}
              >
                {dialogContent}
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

RegularTextField.propTypes = {
  isNumber: PropTypes.bool,
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  helperText: PropTypes.string,
  placeholder: PropTypes.string,
  validators: PropTypes.array,
  errorMessages: PropTypes.array,
  required: PropTypes.bool,
  dialogTitleTH: PropTypes.string,
  dialogFormat: PropTypes.string,
  dialogContent: PropTypes.node,
  dialogWidth: PropTypes.string,
  width: PropTypes.string,
};

export default withWidth()(RegularTextField);
