import React, { PureComponent } from 'react';
import { Popper, Paper, ListItemText, ListItem } from '@material-ui/core';
import amphoes from '../../../../../jsonData/amphoe.json';
import provinces from '../../../../../jsonData/province.json';

class TambonSelect extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      anchorElWidth: '',
      open: false,
    };
  }

  componentDidMount() {
    this.setState({
      anchorElWidth: this.props.anchorEl.offsetWidth,
    });
  }

  render() {
    const {
      selected,
      searchInput,
      handleSelect,
      filteredTambons,
      anchorEl,
      anchorElWidth,
    } = this.props;

    return selected || searchInput === '' ? (
      ''
    ) : (
      <Popper open anchorEl={anchorEl} style={{ zIndex: 2 }}>
        <Paper
          style={{
            width: `${anchorElWidth}px`,
            overflowY: 'auto',
            maxHeight: '290px',
          }}
        >
          {filteredTambons.slice(0, 20).map(tambon => (
            <ListItem
              button
              key={tambon.pid}
              onClick={handleSelect}
              value={tambon.name}
            >
              <ListItemText
                primary={tambon.name}
                secondary={`${
                  amphoes.find(amphoe => amphoe.pid === tambon.amphoe_pid)
                    .name
                } > ${
                  provinces.find(
                    province => province.pid === tambon.changwat_pid
                  ).name
                } `}
              />
            </ListItem>
          ))}
        </Paper>
      </Popper>
    );
  }
}

export default TambonSelect;
