import React, { PureComponent } from 'react';

import CalculatorImg from '../../../../../images/calculator.png';
import {
  InputAdornment,
  Tooltip,
  IconButton,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../../shared/AdditionalInfoDialog';
import ChildTextField from '../../../../shared/ChildTextField';
import { n16_2R } from '../../../../shared/CommonValidators';

class TotalPaymentAmount extends PureComponent {
  constructor() {
    super();
    this.state = {
      totalPaymentAmount: '',
      hasError: false,
      helperText: 'ยอดชำระเงินรวม',
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  round = value => {
    return Math.round(Math.round(value * 1000) / 10) / 100;
  };

  calculateTotal = (tax = 0, card = 0, deposit = 0) => {
    this.setState({
      totalPaymentAmount: this.round(
        parseFloat(tax) - (parseFloat(card) + parseFloat(deposit))
      ).toString(),
    });
  };

  handleCalculate = () => {
    let totalTax = this.props.totalTaxValue;
    let cardPaymentAmount = this.props.cardValue;
    let totalDeposit = this.props.totalDepositValue;

    totalTax = totalTax === '' ? 0 : totalTax;
    cardPaymentAmount = cardPaymentAmount === '' ? 0 : cardPaymentAmount;
    totalDeposit = totalDeposit === '' ? 0 : totalDeposit;

    this.calculateTotal(totalTax, cardPaymentAmount, totalDeposit);
  };

  render() {
    const { handleChange } = this;
    const { totalPaymentAmount } = this.state;
    return (
      <ChildTextField
        required
        name="totalPaymentAmount"
        handleChange={handleChange}
        value={totalPaymentAmount}
        label="Total Payment Amount"
        placeholder="0000.00"
        validators={n16_2R.validators}
        errorMessages={n16_2R.errorMessages}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="คำนวณ">
                <IconButton onClick={this.handleCalculate}>
                  <img
                    alt="calculateBtn"
                    width="16.67"
                    height="16.67"
                    src={CalculatorImg}
                  />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="ยอดชำระเงินรวม"
                titleEN="Total Payment Amount"
                format="n..16,2"
                maxWidth="xs"
              >
                <Typography>
                  มีค่าเท่ากับ Total Tax - ( Total Tax Card Payment Amount
                  + Total Deposit)
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default TotalPaymentAmount;
