import React, { PureComponent } from 'react';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../../shared/AdditionalInfoDialog';
import exchangeRateData from '../../../../../jsonData/exchangeRateData.json';
import AutoRenewIcon from '@material-ui/icons/Autorenew';
import { handleChange } from '../../../../shared/handleChange';
import ChildTextField from '../../../../shared/ChildTextField';
import { n12_5R } from '../../../../shared/CommonValidators';

class ExchangeRate extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
      hasError: false,
      helperText: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  setExchangeRate = currencyObj => {
    this.setState({
      value: parseFloat(currencyObj.value).toString(),
      hasError: false,
      helperText: '',
    });
  };

  displayError = () => {
    this.setState({
      value: '',
      hasError: true,
      helperText: 'กรุณาเลือกสกุลเงินก่อน',
    });
  };

  getExchangeRate = () => {
    const currency = document.getElementById('currencyCode');
    const currencyObj = exchangeRateData.find(
      element => element.name === currency.value
    );
    currencyObj ? this.setExchangeRate(currencyObj) : this.displayError();
  };

  render() {
    const { hasError, value, helperText } = this.state;
    const { handleChange, getExchangeRate } = this;
    return (
      <ChildTextField
        required
        error={hasError}
        name="exchangeRate"
        handleChange={handleChange}
        value={value}
        label="Exchange Rate"
        helperText={helperText}
        placeholder="0000.00000"
        validators={n12_5R.validators}
        errorMessages={n12_5R.errorMessages}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="ดึงค่าอัตราแลกเปลี่ยน">
                <IconButton onClick={getExchangeRate}>
                  <AutoRenewIcon />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="อัตราแลกเปลี่ยนเงินตรา"
                titleEN="Exchange Rate"
                format="n..12,5"
                maxWidth="xs"
              >
                <Typography gutterBottom>
                  ข้อมูลอัตราแลกเปลี่ยนเดือนมกราคม 2019
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default ExchangeRate;
