import React from 'react';
import ChildSelect from '../../../../shared/ChildSelect';

const GuaranteeType = React.memo(props => {
  const { handleChange, guaranteeType, guaranteeTypeOptions } = props;

  return guaranteeTypeOptions.length === 0 ? null : (
    <ChildSelect
      required
      name="guaranteeType"
      handleChange={handleChange}
      value={guaranteeType}
      label="Guarantee Type"
      options={guaranteeTypeOptions}
    />
  );
});

export default GuaranteeType;
