import React, { PureComponent, Fragment } from 'react';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { Divider, withStyles, Collapse } from '@material-ui/core';
import ForwardingChargeCurrencyCode from './optionalChargeFields/ForwardingChargeCurrencyCode';
import ForwardingChargeForeign from './optionalChargeFields/ForwardingChargeForeign';
import FieldHeader from '../../../../shared/FieldHeader';
import PackingChargeCurrencyCode from './optionalChargeFields/PackingChargeCurrencyCode';
import PackingChargeForeign from './optionalChargeFields/PackingChargeAmountForeign';
import InteriorTransportCurrencyCode from './optionalChargeFields/InteriorTransportCurrencyCode';
import InteriorTransportAmountForeign from './optionalChargeFields/InteriorTransportAmountForeign';
import LandingChargeCurrencyCode from './optionalChargeFields/LandingChargeCurrencyCode';
import LandingChargeAmountForeign from './optionalChargeFields/LandingChargeAmountForeign';
import OtherChargeCurrencyCode from './optionalChargeFields/OtherChargeCurrencyCode';
import OtherChargeAmountForeign from './optionalChargeFields/OtherChargeAmountForeign';
import DetailOfOtherCharge from './optionalChargeFields/DetailOfOtherCharge';

const styles = theme => ({
  divider: {
    marginTop: theme.spacing.unit * 1.5,
    marginBottom: theme.spacing.unit * 1.5,
  },
  formControl: {
    marginTop: theme.spacing.unit,
    marginLeft: theme.spacing.unit,
  },
  formLabel: {
    marginBottom: theme.spacing.unit,
  },
});

class OptionalChargeControl extends PureComponent {
  state = {
    forward: false,
    packing: false,
    interiorTP: false,
    landing: false,
    other: false,
  };

  handleChange = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  render() {
    const { classes } = this.props;
    const { forward, packing, interiorTP, landing, other } = this.state;
    return (
      <Fragment>
        <Divider className={classes.divider} />
        <FormControl component="fieldset" className={classes.formControl}>
          <FormLabel component="legend" className={classes.formLabel}>
            กรุณาเลือกค่าใช้จ่ายที่มี
          </FormLabel>
          <FormGroup>
            <FormControlLabel
              control={
                <Checkbox
                  checked={forward}
                  onChange={this.handleChange('forward')}
                  value="forward"
                />
              }
              label="Forwarding (ค่าใช้จ่ายโรงงานถึงท่าเรือ)"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={packing}
                  onChange={this.handleChange('packing')}
                  value="packing"
                />
              }
              label="Packing (ค่าบรรจุ)"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={interiorTP}
                  onChange={this.handleChange('interiorTP')}
                  value="interiorTP"
                />
              }
              label="Interior Transport (ค่าขนส่งในประเทศผู้ขาย)"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={landing}
                  onChange={this.handleChange('landing')}
                  value="landing"
                />
              }
              label="Landing (ค่าขนย้ายในประเทศผู้ขาย)"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={other}
                  onChange={this.handleChange('other')}
                  value="other"
                />
              }
              label="Other (ค่าใช้จ่ายอื่นๆ)"
            />
          </FormGroup>
        </FormControl>
        <Collapse in={forward}>
          <FieldHeader>ค่าใช้จ่ายโรงงานถึงท่าเรือ</FieldHeader>
          <ForwardingChargeCurrencyCode />
          <ForwardingChargeForeign />
        </Collapse>
        <Collapse in={packing}>
          <FieldHeader>ค่าบรรจุ</FieldHeader>
          <PackingChargeCurrencyCode />
          <PackingChargeForeign />
        </Collapse>
        <Collapse in={interiorTP}>
          <FieldHeader>ค่าขนส่งภายในประเทศผู้ขาย</FieldHeader>
          <InteriorTransportCurrencyCode />
          <InteriorTransportAmountForeign />
        </Collapse>
        <Collapse in={landing}>
          <FieldHeader>ค่าขนย้ายสินค้าในประเทศผู้ขาย</FieldHeader>
          <LandingChargeCurrencyCode />
          <LandingChargeAmountForeign />
        </Collapse>
        <Collapse in={other}>
          <FieldHeader>ค่าใช้จ่ายอื่นๆ</FieldHeader>
          <OtherChargeCurrencyCode />
          <OtherChargeAmountForeign />
          <DetailOfOtherCharge disabled={!other} />
        </Collapse>
        <Divider className={classes.divider} />
      </Fragment>
    );
  }
}

export default withStyles(styles)(OptionalChargeControl);
