import React, { PureComponent, Fragment } from 'react';
import {
  RadioGroup,
  FormControlLabel,
  Radio,
  FormLabel,
} from '@material-ui/core';
import ChildTextField from '../../../../shared/ChildTextField';

class ShippingMarks extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      radioValue: '',
      readOnly: true,
      multiline: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      shippingMarks: '',
      radioSelected: false,
      placeholder: 'กรุณาเลือกจากด้านบนก่อน',
    };
  }

  handleChange = name => e => {
    this.setState({
      [name]: e.target.value,
    });
  };

  setreadOnlyFieldCase = message => {
    this.setState({
      shippingMarks: message,
      readOnly: true,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      multiline: false,
      placeholder: '',
    });
  };

  setEnabledFieldCase = () => {
    this.setState({
      shippingMarks: '',
      readOnly: false,
      validators: ['required', 'maxStringLength: 512'],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้สูงสุด 512 ตัวอักษร'],
      multiline: true,
      placeholder: 'ระบุรายละเอียด สูงสุด 512 ตัวอักษร',
    });
  };

  handleRadioSelect = e => {
    const { setreadOnlyFieldCase, setEnabledFieldCase } = this;
    const radioValue = e.target.value;
    this.setState({ radioValue });
    switch (radioValue) {
      case '0':
        setreadOnlyFieldCase('NO SHIPPING MARK');
        break;
      case '1':
      case '3':
        setreadOnlyFieldCase('PICTURE');
        break;
      case '2':
        setEnabledFieldCase();
        break;
      default:
        break;
    }
  };

  render() {
    const { handleChange, handleRadioSelect } = this;
    const {
      radioValue,
      shippingMarks,
      multiline,
      readOnly,
      validators,
      errorMessages,
      placeholder,
    } = this.state;
    return (
      <Fragment>
        <FormLabel style={{ margin: 8 }}>
          เครื่องหมายและเลขหมายหีบห่อ
        </FormLabel>
        <RadioGroup
          style={{ margin: 8 }}
          aria-label="Shipping Marks Radio"
          name="shippingMarkRadio"
          value={radioValue}
          onChange={handleRadioSelect}
        >
          <FormControlLabel
            value="0"
            control={<Radio />}
            label="ไม่มี Shipping Mark"
          />
          <FormControlLabel
            value="1"
            control={<Radio />}
            label="เป็นรูปภาพ"
          />
          <FormControlLabel
            value="2"
            control={<Radio />}
            label="เป็นข้อความ"
          />
          <FormControlLabel
            value="3"
            control={<Radio />}
            label="เป็นภาษาต่างประเทศที่กรอกไม่ได้"
          />
        </RadioGroup>
        <ChildTextField
          required
          name="shippingMarks"
          handleChange={handleChange('shippingMarks')}
          value={shippingMarks}
          label="Shipping Marks"
          placeholder={placeholder}
          multiline={multiline}
          rows="8"
          readOnly={readOnly}
          validators={validators}
          errorMessages={errorMessages}
        />
      </Fragment>
    );
  }
}

export default ShippingMarks;
