import React, { PureComponent } from 'react';
import ChildTextField from '../../../../shared/ChildTextField';
import { an35R } from '../../../../shared/CommonValidators';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import LabelIcon from '@material-ui/icons/Label';
import LabelOffIcon from '@material-ui/icons/LabelOff';
import AdditionalInfoDialog from '../../../../shared/AdditionalInfoDialog';

class BrandName extends PureComponent {
  state = {
    hasBrandName: true,
    readOnly: false,
    value: '',
  };

  handleChange = e => {
    this.setState({
      value: e.target.value.trim(),
    });
  };

  handleClick = () => {
    this.setState(state => ({
      hasBrandName: !state.hasBrandName,
      value: !state.hasBrandName ? '' : 'NO BRAND',
      readOnly: !state.hasBrandName ? false : true,
    }));
  };

  render() {
    return (
      <ChildTextField
        required
        name="brandName"
        label="Brand Name"
        placeholder="เครื่องหมายการค้า"
        handleChange={this.handleChange}
        value={this.state.value}
        validators={an35R.validators}
        errorMessages={an35R.errorMessages}
        InputProps={{
          readOnly: this.state.readOnly,
          endAdornment: (
            <InputAdornment position="end">
              {this.state.hasBrandName ? (
                <Tooltip
                  disableFocusListener
                  disableTouchListener
                  title="มีแบรนด์เนม"
                >
                  <IconButton onClick={this.handleClick}>
                    <LabelIcon fontSize="small" />{' '}
                  </IconButton>
                </Tooltip>
              ) : (
                <Tooltip
                  disableFocusListener
                  disableTouchListener
                  title="ไม่มีแบรนด์เนม"
                >
                  <IconButton onClick={this.handleClick}>
                    <LabelOffIcon fontSize="small" />
                  </IconButton>
                </Tooltip>
              )}

              <AdditionalInfoDialog
                maxWidth="xs"
                titleTH="เครื่องหมายการค้า"
                titleEN="Brand Name"
                format="an..35"
              >
                <Typography>
                  สําหรับรถยนต์ ระบุยี่ห้อและรุ่นของรถยนต์
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default BrandName;
