import React, { PureComponent } from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem, withWidth } from '@material-ui/core';
import PropTypes from 'prop-types';

class ChildSelect extends PureComponent {
  componentDidMount() {
    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  render() {
    const {
      name,
      label,
      helperText,
      options,
      width,
      handleChange,
      value,
      readOnly,
      required,
    } = this.props;
    return (
      <SelectValidator
        id={name}
        required={required}
        SelectProps={{
          native: width === 'xs' ? true : false,
        }}
        InputLabelProps={
          width === 'xs'
            ? {
                shrink: true,
              }
            : null
        }
        InputProps={{
          readOnly: readOnly,
        }}
        name={name}
        onChange={handleChange}
        value={value}
        select
        label={label}
        helperText={helperText}
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        variant="outlined"
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        margin={width === 'xs' ? 'normal' : 'dense'}
      >
        {options.map(option =>
          width !== 'xs' ? (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ) : (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          )
        )}
      </SelectValidator>
    );
  }
}

ChildSelect.defaultProps = {
  required: true,
};

ChildSelect.propTypes = {
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  helperText: PropTypes.string,
  options: PropTypes.array.isRequired,
  width: PropTypes.string,
  handleChange: PropTypes.func,
  value: PropTypes.string,
  readOnly: PropTypes.bool,
  required: PropTypes.bool,
};

export default withWidth()(ChildSelect);
