import React from 'react';
import currencyCodes from '../../jsonData/currencycodes.json';
import SearchSelector from './SearchSelector';

const CurrencySelector = props => {
  const { name, label, placeholder, required, disabled } = props;
  return (
    <SearchSelector
      required={required}
      disabled={disabled}
      id={name}
      label={label}
      placeholder={placeholder}
      codeFirst={false}
      firstDataProp="currencyCode"
      secondDataProp="currencyName"
      thirdDataProp="name"
      maxDigits={3}
      infoData={currencyCodes}
    />
  );
};

export default CurrencySelector;
