import React, { Fragment, PureComponent } from 'react';
import tambons from '../../../../../jsonData/tambon';
import { TextValidator } from 'react-material-ui-form-validator';
import { withWidth } from '@material-ui/core';
import TambonSelect from './TambonSelection';

class AddressSelectors extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      selectedTambon: '',
      selectedAmphoe: '',
      selectedProvince: '',
      searchInput: '',
      selected: false,
      anchorEl: '',
      anchorElWidth: '',
      filteredTambons: [],
    };
  }

  handleSeachInput = e => {
    const { target, currentTarget } = e;
    this.setState({
      selected: false,
      searchInput: target.value,
      anchorEl: currentTarget,
      anchorElWidth: currentTarget.offsetWidth,
      filteredTambons: tambons.filter(tambon =>
        tambon.name.includes(target.value)
      ),
    });
  };

  handleChange = name => e => {
    this.setState({
      [name]: e.target.value,
    });
  };

  updateSelectedTambon = e => {
    this.setState({
      selectedTambon: e.currentTarget.innerText.split('\n')[0],
    });
  };

  updateSelectedAmphoe = e => {
    this.setState({
      selectedAmphoe: e.currentTarget.innerText
        .split('\n')[1]
        .split('>')[0]
        .trim(),
    });
  };

  updateSelectedProvince = e => {
    this.setState({
      selectedProvince: e.currentTarget.innerText
        .split('\n')[1]
        .split('>')[1]
        .trim(),
    });
  };

  handleSelect = e => {
    this.updateSelectedTambon(e);
    this.updateSelectedAmphoe(e);
    this.updateSelectedProvince(e);
    this.setState({
      selected: true,
      searchInput: e.currentTarget.innerText.split('\n')[0],
    });
  };

  render() {
    const {
      searchInput,
      selected,
      selectedAmphoe,
      filteredTambons,
      selectedProvince,
      anchorEl,
      anchorElWidth,
    } = this.state;
    const { width } = this.props;
    const { handleSelect, handleSeachInput, handleChange } = this;

    return (
      <Fragment>
        <TextValidator
          id="companyTambon"
          name="companyTambon"
          fullWidth
          label="ตำบล / แขวง"
          placeholder="กรอกชื่อตำบล / แขวง เพื่อค้นหา"
          type="search"
          value={searchInput}
          onChange={handleSeachInput}
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          margin={width === 'xs' ? 'normal' : 'dense'}
        />
        <TambonSelect
          anchorElWidth={anchorElWidth}
          anchorEl={anchorEl}
          searchInput={searchInput}
          selected={selected}
          handleSelect={handleSelect}
          filteredTambons={filteredTambons}
        />
        <TextValidator
          id="subProvince"
          name="subProvince"
          onChange={handleChange('selectedAmphoe')}
          value={selectedAmphoe}
          label="อำเภอ / เขต"
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          margin={width === 'xs' ? 'normal' : 'dense'}
        />
        <TextValidator
          id="province"
          name="province"
          onChange={handleChange('selectedProvince')}
          value={selectedProvince}
          label="จังหวัด"
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          margin={width === 'xs' ? 'normal' : 'dense'}
        />
      </Fragment>
    );
  }
}

export default withWidth()(AddressSelectors);
