import React, { Fragment } from 'react';
import {
  Divider,
  DialogActions,
  Button,
  DialogContent,
  DialogTitle,
  Typography,
  Dialog,
} from '@material-ui/core';

const InfoDialog = props => {
  const {
    maxWidth,
    titleEN,
    titleTH,
    content,
    format,
    handleClose,
    open,
  } = props;
  return (
    <Dialog
      disableRestoreFocus
      scroll="paper"
      maxWidth={maxWidth}
      open={open}
      onClose={handleClose}
    >
      <DialogTitle>
        <Fragment>
          {titleTH}
          <Typography
            component="span"
            color="textSecondary"
            variant="subtitle2"
          >
            {titleEN}
          </Typography>
        </Fragment>
        <Divider style={{ marginTop: 15 }} />
      </DialogTitle>
      <DialogContent>
        <Typography color="textSecondary" gutterBottom variant="subtitle1">
          คำอธิบาย
        </Typography>
        <Fragment>{content}</Fragment>
        <br />
        <Typography gutterBottom color="textSecondary" variant="subtitle1">
          รูปแบบการกรอก
        </Typography>
        <Typography color="textPrimary">{format}</Typography>
        <DialogActions>
          <Button
            variant="outlined"
            size="medium"
            color="primary"
            onClick={handleClose}
          >
            ปิด
          </Button>
        </DialogActions>
      </DialogContent>
    </Dialog>
  );
};

export default InfoDialog;
