import React, { PureComponent } from 'react';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../../shared/AdditionalInfoDialog';
import CalculatorImg from '../../../../../images/calculator.png';
import { n16_2R } from '../../../../shared/CommonValidators';
import ChildTextField from '../../../../shared/ChildTextField';

class CIFValueBaht extends PureComponent {
  state = {
    cifValueBaht: '',
    hasError: false,
    helperText: '',
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  multiplyFloat = (first, second) => {
    const result = parseFloat(first) * parseFloat(second);
    const roundedResult = Math.round(Math.round(result * 1000) / 10) / 100;
    return roundedResult.toString();
  };

  displayError = () => {
    this.setState({
      helperText: 'ตรวจสอบช่อง Exchange Rate และ CIF Value Foreign',
      hasError: true,
      cifValueBaht: '',
    });
  };

  displayNegativeError = () => {
    this.setState({
      helperText: 'มีจำนวนติดลบ',
      hasError: true,
      cifValueBaht: '',
    });
  };

  getCIFValueBaht = () => {
    const exchangeRate = document.getElementById('exchangeRate').value;
    const cifValueForeign = document.getElementById('cifValueForeign')
      .value;

    if (
      isNaN(parseFloat(exchangeRate)) ||
      isNaN(parseFloat(cifValueForeign))
    ) {
      this.displayError();
      return;
    }

    if (exchangeRate.length > 0 && cifValueForeign.length > 0) {
      if (
        parseFloat(exchangeRate) < 0 ||
        parseFloat(cifValueForeign) < 0
      ) {
        this.displayNegativeError();
        return;
      }
      this.setState({
        cifValueBaht: this.multiplyFloat(exchangeRate, cifValueForeign),
        hasError: false,
        helperText: '',
      });
    } else {
      this.displayError();
    }
  };

  render() {
    const { handleChange } = this;
    const { cifValueBaht, hasError, helperText } = this.state;
    return (
      <ChildTextField
        required
        error={hasError}
        name="cifValueBaht"
        handleChange={handleChange}
        value={cifValueBaht}
        label="CIF Value Baht"
        helperText={helperText}
        placeholder="0000.00"
        validators={n16_2R.validators}
        errorMessages={n16_2R.errorMessages}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="คำนวณ">
                <IconButton onClick={this.getCIFValueBaht}>
                  <img
                    alt="calculateBtn"
                    width="16.67"
                    height="16.67"
                    src={CalculatorImg}
                  />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="ราคา CIF เงินบาท"
                titleEN="CIF Value Baht"
                format="n..16,2"
                maxWidth="xs"
              >
                <Typography>
                  มีค่าเท่ากับ Exchange Rate คูณ CIF Value Foreign
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default CIFValueBaht;
