import React, { Fragment, PureComponent } from 'react';
import {
  Card,
  CardHeader,
  CardContent,
  Typography,
  Divider,
  Drawer,
  IconButton,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

class BottomDrawer extends PureComponent {
  render() {
    const {
      titleEN,
      titleTH,
      content,
      format,
      drawerOpen,
      drawerClose,
    } = this.props;
    return (
      <div>
        <Drawer anchor="bottom" open={drawerOpen} onClose={drawerClose}>
          <Card style={{ overflowY: 'auto' }}>
            <CardHeader
              title={<Typography variant="h6">{titleTH}</Typography>}
              action={
                <IconButton onClick={drawerClose}>
                  <CloseIcon />
                </IconButton>
              }
              subheader={
                <Typography color="textSecondary" variant="subtitle1">
                  {titleEN}
                </Typography>
              }
            />
            <Divider variant="middle" />
            <CardContent>
              <Fragment>{content}</Fragment>
              <br />
              <Typography
                gutterBottom
                color="textSecondary"
                variant="subtitle1"
              >
                รูปแบบการกรอก
              </Typography>
              <Typography color="textPrimary">{format}</Typography>
            </CardContent>
          </Card>
        </Drawer>
      </div>
    );
  }
}

export default BottomDrawer;
