import React, { PureComponent } from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem, withWidth } from '@material-ui/core';
import { handleChange } from './handleChange';
import PropTypes from 'prop-types';

class RegularSelect extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
      currentWidth: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  componentDidMount() {
    this.updateInterval = setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  componentWillUnmount() {
    clearTimeout(this.updateInterval);
  }

  render() {
    const { name, label, helperText, options, width } = this.props;
    return (
      <SelectValidator
        id={name}
        required
        SelectProps={{
          native: width === 'xs' ? true : false,
        }}
        InputLabelProps={
          width === 'xs'
            ? {
                shrink: true,
              }
            : null
        }
        name={name}
        onChange={this.handleChange}
        value={this.state.value}
        select
        label={label}
        helperText={helperText}
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        variant="outlined"
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        margin={width === 'xs' ? 'normal' : 'dense'}
      >
        {options.map(option =>
          width !== 'xs' ? (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ) : (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          )
        )}
      </SelectValidator>
    );
  }
}

RegularSelect.propTypes = {
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  helperText: PropTypes.string,
  options: PropTypes.array.isRequired,
  width: PropTypes.string,
};

export default withWidth()(RegularSelect);
