import React, { PureComponent, Fragment } from 'react';
import portCodes from '../../../../../../jsonData/portCodes.json';
import { TextValidator } from 'react-material-ui-form-validator';
import {
  ListItem,
  ListItemText,
  withWidth,
  InputAdornment,
} from '@material-ui/core';
import SearchSelectWrapper from '../../../../../shared/searchSelectorComponents/SearchSelectWrapper.js';
import AdditionalInfoDialog from '../../../../../shared/AdditionalInfoDialog.js';

class PortSearch extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      portCodesData: [],
      searchInput: '',
      selected: false,
      validators: [],
      errorMessages: [],
      anchorEl: null,
      filteredPortCodes: [],
    };
  }

  componentDidMount() {
    const { required } = this.props;
    this.interval = null;
    this.setState({
      validators: required ? ['required'] : [],
      errorMessages: required ? ['จำเป็นต้องกรอก'] : [],
    });
  }

  componentWillUnmount() {
    clearTimeout(this.interval);
  }

  handleBlur = e => {
    e.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 225);
  };

  handleSeachInput = e => {
    const { currentTarget } = e;
    const { value } = e.target;
    e.preventDefault();
    this.setState({
      searchInput: value,
      selected: false,
      anchorEl: currentTarget.parentElement,
      anchorElWidth: currentTarget.parentElement.offsetWidth,
      filteredPortCodes: this.filterPortCodes(value),
    });
  };

  filterPortCodes = input => {
    const { isRelease } = this.props;
    const lowerCasedInput = input.toLowerCase();

    return isRelease
      ? portCodes.filter(
          port =>
            port.releasePort === 'Y' &&
            (port.portNumber.toLowerCase().includes(lowerCasedInput) ||
              port.portName.toLowerCase().includes(lowerCasedInput))
        )
      : portCodes.filter(
          port =>
            port.dischargePort === 'Y' &&
            (port.portNumber.toLowerCase().includes(lowerCasedInput) ||
              port.portName.toLowerCase().includes(lowerCasedInput))
        );
  };

  handleSelect = e => {
    const { required } = this.props;
    e.preventDefault();
    this.setState({
      selected: true,
      searchInput: e.currentTarget.innerText.trim().slice(0, 4),
      validators: required ? ['required', 'maxStringLength: 4'] : [],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้ 4 ตัวอักษร'],
    });
  };

  render() {
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const {
      id,
      label,
      helperText,
      required,
      width,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
    } = this.props;

    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      filteredPortCodes,
      anchorEl,
      anchorElWidth,
    } = this.state;

    return (
      <Fragment>
        <TextValidator
          id={id}
          name={id}
          fullWidth
          required={required}
          label={label}
          helperText={helperText}
          placeholder="ค้นหาด้วยรหัสสถานที่ / ชื่อสถานที่"
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
          variant="outlined"
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          margin={width === 'xs' ? 'normal' : 'dense'}
          InputProps={{
            endAdornment: dialogContent && (
              <InputAdornment position="end">
                <AdditionalInfoDialog
                  maxWidth={dialogWidth ? dialogWidth : 'xs'}
                  titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                  titleEN={label}
                  format={dialogFormat}
                >
                  {dialogContent}
                </AdditionalInfoDialog>
              </InputAdornment>
            ),
          }}
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <SearchSelectWrapper
            anchorEl={anchorEl}
            anchorElWidth={anchorElWidth}
          >
            {filteredPortCodes.slice(0, 20).map(port => (
              <ListItem
                button
                key={port.portNumber}
                onClick={handleSelect}
                value={port.portNumber}
              >
                <ListItemText
                  primary={`${port.portNumber}`}
                  secondary={`${port.portName}`}
                />
              </ListItem>
            ))}
          </SearchSelectWrapper>
        )}
      </Fragment>
    );
  }
}

export default withWidth()(PortSearch);
