import React, { PureComponent } from 'react';
import { DatePicker } from 'material-ui-pickers';
import { Grid, IconButton, withWidth, Tooltip } from '@material-ui/core';
import DateRangeIcon from '@material-ui/icons/DateRange';

class DateSelector extends PureComponent {
  state = {
    selectedDate: new Date(),
  };

  handleDateChange = date => {
    this.setState({
      selectedDate: date,
    });
  };

  openPicker = e => {
    this.picker.open(e);
  };

  handleClick = () => {
    return;
  };

  render() {
    const { selectedDate } = this.state;
    const {
      id,
      label,
      InputProps,
      hasAdornment,
      helperText,
      width,
    } = this.props;
    return hasAdornment ? (
      <Grid container spacing={16} alignItems="center">
        <Grid item sm={1} xs={2}>
          <Tooltip
            disableFocusListener
            disableTouchListener
            title="เลือกวันที่"
          >
            <IconButton onClick={this.openPicker}>
              <DateRangeIcon />
            </IconButton>
          </Tooltip>
        </Grid>
        <Grid item sm={11} xs={10}>
          <DatePicker
            emptyLabel="คลิกไอคอนเพื่อเลือกวันที่"
            id={id}
            label={label}
            format="yyyy/MM/dd"
            name={id}
            fullWidth
            value={selectedDate}
            onChange={this.handleDateChange}
            variant="outlined"
            InputProps={InputProps}
            onClick={this.handleClick}
            ref={node => {
              this.picker = node;
            }}
            style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
            margin={width === 'xs' ? 'normal' : 'dense'}
          />
        </Grid>
      </Grid>
    ) : (
      <DatePicker
        id={id}
        label={label}
        format="yyyy/MM/dd"
        placeholder="2018/12/31"
        name={id}
        helperText={helperText}
        fullWidth
        value={selectedDate}
        onChange={this.handleDateChange}
        variant="outlined"
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        margin={width === 'xs' ? 'normal' : 'dense'}
      />
    );
  }
}

export default withWidth()(DateSelector);
