import React, { PureComponent, Fragment } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../../shared/FieldHeader';
import { addCustomValidationRules } from '../../shared/customValidations';
import PaymentMethod from '../fields/IDC1Fields/idc4Fields/PaymentMethod';
import GuaranteeMethod from '../fields/IDC1Fields/idc4Fields/GuaranteeMethod';
import guaranteeMethodsData from '../fields/IDC1Fields/idc4Fields/data/guaranteeMethodsData';
import guaranteeTypesData from '../fields/IDC1Fields/idc4Fields/data/guaranteeTypesData';
import GuaranteeType from '../fields/IDC1Fields/idc4Fields/GuaranteeType';
import BankFields from '../fields/IDC1Fields/idc4Fields/BankFields';
import GuaranteeBankCode from '../fields/IDC1Fields/idc4Fields/GuaranteeBankCode';
import GuaranteeBankBranchCode from '../fields/IDC1Fields/idc4Fields/GuaranteeBankBranchCode';
import GuaranteeBankAccountNumber from '../fields/IDC1Fields/idc4Fields/GuaranteeBankAccountNumber';
import TotalDepositAmount from '../fields/IDC1Fields/idc4Fields/TotalDepositAmount';
import TotalTax from '../fields/IDC1Fields/idc4Fields/TotalTax';
import TotalDeposit from '../fields/IDC1Fields/idc4Fields/TotalDeposit';
import SubmitButton from '../../shared/SubmitButton';
import { withSnackbar } from 'notistack';

class IDC1FormWrapper4 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      paymentMethod: '',
      guaranteeMethod: '',
      guaranteeMethodOptions: [],
      guaranteeTypeOptions: [],
      guaranteeType: '',
      totalTax: '',
      totalDeposit: '',
    };
    this.paymentRef = React.createRef();
  }

  componentWillMount() {
    addCustomValidationRules(this);
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  setGuaranteeTypeOptions = choosingArray => {
    this.setState({
      guaranteeTypeOptions: guaranteeTypesData.filter(val =>
        choosingArray.includes(val.value)
      ),
    });
  };

  filterGuaranteeType = e => {
    const { setGuaranteeTypeOptions } = this;
    const guaranteeMethod = e.target.value;
    const payment = this.paymentRef.current.props.paymentMethod;

    if (guaranteeMethod === 'L') {
      this.setState({
        guaranteeTypeOptions: [],
        guaranteeType: '',
      });
      return;
    }

    if (payment === 'A') {
      if (guaranteeMethod === 'A') {
        setGuaranteeTypeOptions(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        setGuaranteeTypeOptions(['E']);
      }
    } else if (payment === 'H') {
      if (guaranteeMethod === 'H') {
        setGuaranteeTypeOptions(['C', 'E']);
      }
    } else if (payment === 'L') {
      if (guaranteeMethod === 'A') {
        setGuaranteeTypeOptions(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        setGuaranteeTypeOptions(['E', 'C']);
      }
    }
  };

  filterGuaranteeMethods = e => {
    if (e.target.value === 'H') {
      this.setState({
        guaranteeMethodOptions: guaranteeMethodsData.filter(
          data => data.value !== 'A'
        ),
      });
    } else {
      this.setState({
        guaranteeMethodOptions: guaranteeMethodsData,
      });
    }
  };

  handleGuaranteeMethodChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
    this.filterGuaranteeType(e);
  };

  handlePaymentMethodChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
    this.filterGuaranteeMethods(e);
  };

  handleSubmit = e => {
    const { enqueueSnackbar } = this.props;
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
    enqueueSnackbar('บันทึกข้อมูลเรียบร้อย');
  };

  render() {
    const {
      handleChange,
      handleSubmit,
      handlePaymentMethodChange,
      handleGuaranteeMethodChange,
    } = this;

    const {
      paymentMethod,
      guaranteeMethod,
      guaranteeMethodOptions,
      guaranteeType,
      guaranteeTypeOptions,
      totalTax,
      totalDeposit,
    } = this.state;

    const { enqueueSnackbar } = this.props;

    return (
      <ValidatorForm
        onError={error =>
          enqueueSnackbar(`มี ${error.length} ข้อผิดพลาด กรุณาตรวจสอบ`)
        }
        onSubmit={handleSubmit}
        noValidate
        autoComplete="off"
      >
        <FieldHeader>รูปแบบการชำระเงิน</FieldHeader>
        <PaymentMethod
          ref={this.paymentRef}
          handleChange={handlePaymentMethodChange}
          paymentMethod={paymentMethod}
        />
        <GuaranteeMethod
          handleChange={handleGuaranteeMethodChange}
          guaranteeMethod={guaranteeMethod}
          guaranteeMethodOptions={guaranteeMethodOptions}
        />
        <GuaranteeType
          handleChange={handleChange}
          guaranteeType={guaranteeType}
          guaranteeTypeOptions={guaranteeTypeOptions}
        />
        {/* Payment Fields */}
        <FieldHeader>การชำระค่าภาษี</FieldHeader>
        <TotalTax handleChange={handleChange} totalTax={totalTax} />
        <TotalDeposit
          handleChange={handleChange}
          totalDeposit={totalDeposit}
        />
        <BankFields
          handleChange={handleChange}
          guaranteeType={guaranteeType}
          guaranteeMethod={guaranteeMethod}
          paymentMethod={paymentMethod}
          totalTaxValue={totalTax}
          totalDepositValue={totalDeposit}
        />

        {/* Guarantee Fields */}
        {guaranteeType === 'E' && (
          <Fragment>
            <FieldHeader>ธนาคารที่ใช้ค้ำประกัน</FieldHeader>
            <GuaranteeBankCode />
            <GuaranteeBankBranchCode />
            <GuaranteeBankAccountNumber />
            <TotalDepositAmount />
          </Fragment>
        )}
        <SubmitButton />
      </ValidatorForm>
    );
  }
}

export default withSnackbar(IDC1FormWrapper4);
