import React, { Fragment } from 'react';
import ChildSelect from '../../../../shared/ChildSelect';

const GuaranteeMethod = React.memo(props => {
  const { handleChange, guaranteeMethod, guaranteeMethodOptions } = props;
  return (
    <Fragment>
      {guaranteeMethodOptions.length === 0 ? null : (
        <ChildSelect
          name="guaranteeMethod"
          handleChange={handleChange}
          value={guaranteeMethod}
          label="Guarantee Method"
          options={guaranteeMethodOptions}
        />
      )}
    </Fragment>
  );
});

export default GuaranteeMethod;
