import React, { PureComponent } from 'react';
import ChildSelect from '../../../../shared/ChildSelect';

const paymentMethods = [
  {
    value: 'A',
    label: 'A - ชำระที่กรมศุลกากร',
  },
  {
    value: 'H',
    label: 'H - ชำระผ่านธนาคาร',
  },
  {
    value: 'L',
    label: 'L - ไม่มีการชำระ',
  },
];

class PaymentMethod extends PureComponent {
  render() {
    const { handleChange, paymentMethod } = this.props;

    return (
      <ChildSelect
        name="paymentMethod"
        handleChange={handleChange}
        value={paymentMethod}
        label="Payment Method"
        options={paymentMethods}
      />
    );
  }
}

export default PaymentMethod;
