import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment, withWidth } from '@material-ui/core';
import AdditionalInfoDialog from './AdditionalInfoDialog';
import PropTypes from 'prop-types';

class ChildTextField extends PureComponent {
  componentDidMount() {
    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  render() {
    const {
      name,
      label,
      helperText,
      placeholder,
      validators,
      errorMessages,
      required,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
      width,
      handleChange,
      value,
      readOnly,
      multiline,
      rows,
      error,
      InputProps,
    } = this.props;
    return (
      <TextValidator
        name={name}
        id={name}
        error={error}
        required={required}
        onChange={handleChange}
        value={value}
        label={label}
        multiline={multiline}
        rows={rows}
        style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
        helperText={helperText}
        margin={width === 'xs' ? 'normal' : 'dense'}
        placeholder={placeholder}
        variant="outlined"
        fullWidth
        validators={validators}
        errorMessages={errorMessages}
        InputProps={
          InputProps
            ? InputProps
            : {
                readOnly: readOnly,
                endAdornment: dialogContent && (
                  <InputAdornment position="end">
                    <AdditionalInfoDialog
                      maxWidth={dialogWidth ? dialogWidth : 'xs'}
                      titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                      titleEN={label}
                      format={dialogFormat}
                    >
                      {dialogContent}
                    </AdditionalInfoDialog>
                  </InputAdornment>
                ),
              }
        }
      />
    );
  }
}

ChildTextField.propTypes = {
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  helperText: PropTypes.string,
  placeholder: PropTypes.string,
  validators: PropTypes.array,
  errorMessages: PropTypes.array,
  required: PropTypes.bool,
  dialogTitleTH: PropTypes.string,
  dialogFormat: PropTypes.string,
  dialogContent: PropTypes.node,
  dialogWidth: PropTypes.string,
  width: PropTypes.string,
  tooltipTitle: PropTypes.string,
  handleChange: PropTypes.func,
  value: PropTypes.string,
  readOnly: PropTypes.bool,
};

export default withWidth()(ChildTextField);
