import React, { PureComponent, Fragment } from 'react';
import { IconButton, withWidth } from '@material-ui/core';
import Info from '@material-ui/icons/Info';
import { compose } from 'recompose';
import BottomDrawer from './BottomDrawer';
import InfoDialog from './InfoDialog';

class AdditionalInfoDialog extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
  }

  handleClick = () => {
    this.setState(state => ({
      open: !state.open,
    }));
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  render() {
    const {
      maxWidth,
      titleEN,
      titleTH,
      children,
      format,
      width,
    } = this.props;
    return (
      <Fragment>
        <IconButton onClick={this.handleClick}>
          <Info fontSize="small" />
        </IconButton>
        {width === 'xs' ? (
          <BottomDrawer
            titleEN={titleEN}
            titleTH={titleTH}
            content={children}
            format={format}
            drawerOpen={this.state.open}
            drawerClose={this.handleClose}
          />
        ) : (
          <InfoDialog
            maxWidth={maxWidth}
            titleEN={titleEN}
            titleTH={titleTH}
            content={children}
            format={format}
            open={this.state.open}
            handleClose={this.handleClose}
          />
        )}
      </Fragment>
    );
  }
}

export default compose(withWidth())(AdditionalInfoDialog);
